import { createSlice} from '@reduxjs/toolkit';

const initialState = {
    fullName: null,
    age: 0,
    email: null,
    date: null,
    address: null,
    timespan: null
};

const regSlice = createSlice({
    name: 'reg',
    initialState,
    reducers: {
        sendRegData: (state, action) => {
            state.fullName = action.payload.fullName
        }
    }
});

export const {sendRegData}  = regSlice.actions;
export default regSlice.reducer;